<template>
	<view>
		<scroll-view :scroll-y="true"
		style="height: 100vh;"
		 @scrolltolower="getBodyData">
			<view class="content-item" v-for="(item, index) in bodyData.content" :key="index">
				<view class="content-item-in">
					<view class="content-item-in-left">
						<text style="color: #999;">{{ $kt.util.dateFromat(item.gmtCreate,'yyyy-MM-dd hh:mm:ss') }}</text>
						<view style="height: 10rpx;"></view>
						<view class="content-item-in-left-text"
						v-if="item.typeCode=='recharge'"
						>充值</view>
						<view class="content-item-in-left-text"
						v-if="item.typeCode=='giveGift'"
						>赠送礼物</view>
					</view>
					<view class="content-item-in-right">
						<view style="
						position: relative;
						pdding-left: 20rpx;
						padding-right: 20rpx;
						">
							<view style="display: inline-block;
							font-weight: bold;
							color: #666;
							vertical-align: top;">
								<view style="height: 10rpx;"></view>
								<text class="content-item-in-right-text" 
								style="font-size: 35rpx;"
								v-if="item.num > 0">
									{{ "+" }}{{ item.num }}</text>
								<text class="content-item-in-right-text"
								style="font-size: 35rpx;"

								 v-else-if="item.num < 0">
									{{ item.num }}</text>
							</view>
							<o-money size="60rpx"></o-money>
						</view>
					</view>
				</view>
			</view>

			<!-- 加载中 -->
			<view v-if="isLoading" style="text-align: center; padding: 20rpx 0 20rpx 0;">

				<view 
				v-if="bodyData.content.length > 0" 
				style="color: #999;font-size: 28rpx;letter-spacing: 1rpx;">
					正在加载中...</view>
			</view>
		</scroll-view>

		<view v-if="bodyData.content.length == 0 && isEnd" style="position: fixed;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		text-align: center;
		color: #999;
		font-size: 26rpx;
		">

			<view style="position: absolute;
		width: 100%;
		top: 50%;
		transform: translateY(-50%);
		">
				<view>
					<image :src="$kt.file.byPath('/image/icon/detail.svg')" mode="widthFix"
						style="width: 200rpx; height: 200rpx;opacity: 0.5;" />
				</view>
				<view style="height: 20rpx;"></view>
				<view>您还没有明细~</view>
			</view>


		</view>


		<view v-if="bodyData.content.length == 0 && isLoading" style="position: fixed;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		text-align: center;
		color: #333;
		font-size: 26rpx;
		">

			<view style="position: absolute;
		width: 100%;
		top: 50%;
		transform: translateY(-50%);
		">
				<view>
					<image
				v-if="bodyData.content.length == 0" 
				 :src="$kt.file.byPath('/kantboot/icon/loading.svg')"
				 class="loading-icon"
				  mode="widthFix" />
				</view>
			</view>


		</view>

	</view>
</template>

<script>
export default {
	data() {
		return {
			bodyData: {
				content: [],
			},

			requestParam: {
				pageNumber: 1,
			},

			// 请求次数
			requestCount: 0,

			// 是否再加载中
			isLoading: false,

			// 是否加载完成
			isEnd: false,

		}
	},
	created() {
		// 获取缓存数据
		if (uni.getStorageSync('oMoneyDetail')) {
			this.bodyData.content = uni.getStorageSync('oMoneyDetail');
		}


		this.getBodyData();
	},
	methods: {
		scrolltolower(){
			console.log('scrolltolower');
		},
		getBodyData() {
			console.log('getBodyData');
			if (this.isLoading) {
				return null;
			}

			this.isLoading = true;
			this.$kt.request.request({
				api: this.$api.business.ovo.oMoneyDetail.getSelf,
				data: this.requestParam,
				stateSuccess: (res) => {
					if (this.requestCount == 0) {
						this.bodyData.content = []
					}
					this.requestCount++;

					if (res.data.content.length < res.data.size) {
						this.isEnd = true;
					}

					this.bodyData.content = this.bodyData.content.concat(res.data.content);
					if (this.bodyData.content.length < 200) {
						// 缓存数据
						uni.setStorageSync('oMoneyDetail', this.bodyData.content);
					}
					
					this.requestParam.pageNumber++;
					this.isLoading = false;

				},
			});
		},
	}
}
</script>

